<template>
 <div id='app'>
    <div :style="{ backgroundImage: `url('${require('@/assets/background.jpg')}'` }" style="height:100vh; display:flex; justify-content:center; align-items:center; background-size:cover">     
    <div style="display:flex; align-items:center;justify-content:center; background-color:white; width:30% ; height:50%; border-radius:5px">
      <el-form :model="ruleForm" style="width:80%;" :rules="rules" ref="ruleForm"  class="demo-ruleForm" >
      <!-- <el-form > -->
        <div style="font-size:20px ;font-weight:bold ; text-align:center;"></div>
        <el-form-item prop="userid" style="margin-top:20px;">
            <el-input placeholder=" "  v-model="ruleForm.userid"></el-input>
        </el-form-item>

        <el-form-item prop="password">
            <el-input show-password placeholder=" " type="password" v-model="ruleForm.password" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item>                      
            <el-button type="primary" @click="login"></el-button>
            <el-button @click="resetForm('ruleForm')"></el-button>
        </el-form-item>
        <div > 
          <div>  <p class="text-center mt-3">? <a href="#"  @click="toregister"></a></p></div>
          <div> <p class="text-center mt-3">? <a href="#"  @click="handleForgetPass"></a></p></div>
        </div>
      </el-form>
    </div>
  </div>

  <el-dialog title="" :visible.sync="forgetPassDialog" width="30%">
  <el-form :model="forgetUserForm" :rules="rules" ref="forgetUserForm"  label-width="80px" style="padding-right:20px">
    <el-form-item label="" prop="userid" >
      <el-input v-model="forgetUserForm.userid" autocomplete="off" placeholder=""></el-input>
    </el-form-item>
    <el-form-item label="" prop="telephone">
      <el-input v-model="forgetUserForm.telephone" autocomplete="off" placeholder=""></el-input>
    </el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="forgetPassDialog = false"> </el-button>
    <el-button type="primary" @click.prevent="resetPassword"> </el-button>
  </div>
  </el-dialog>


  

 </div>
</template>

<script>
import request from '@/request/request'
 export default {
    name:'app',
    data () {
//id
      var checkId = (rule, value, callback) => {
        const num = Number(value);
        if (isNaN(num)) { // 
          return callback(new Error('')); // 
        }

        if (!Number.isInteger(num)) { // 
          return callback(new Error('')); // 
        }

        if (value.length !== 4) { // 
          return callback(new Error('4')); // 
        }
        return callback();
      };
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          return callback(new Error(''));
        }
        return callback();
      };

       return {
        forgetUserForm:{
          userid:'',
          telephone:''
        },//
        forgetPassDialog:false,
        ruleForm: {
          password: '',
          userid: '',
       },
       rules: {
          password: [
            { validator: validatePass, trigger: 'change' }
          ],
          userid: [
            { validator: checkId, trigger: 'change' }
          ],
          telephone: [
            { required: true, message: '', trigger: 'change' },
            { min: 11, max: 11, message: '11', trigger: 'change' }
          ],
        }
       };
    },
    components: {

    },
    methods: { 
      
      handleForgetPass(){//
      this.forgetUserForm={};
      this.forgetPassDialog = true;
      },

      resetPassword(){
        this.$refs["forgetUserForm"].validate((valid) => {
        if(valid){
        this.$request.put("/user/forgetPass",this.forgetUserForm).then(res=>{
          if(res.code==0){
            this.$message.success("");
            this.forgetPassDialog = false;
          }else{
            this.$message.error(res.msg);
          }
        })
        }
        })
      },
      login(){
        this.$refs["ruleForm"].validate((valid) => {
          // console.log("valid:",valid);
          if (valid){
            request.post('/user/login',this.ruleForm).then(res=>{
              if(res.code==0){
                localStorage.setItem('user',JSON.stringify(res.data));//
                this.$message.success("");
                this.$router.push({path:'/intro'})
                
              }else{
                this.$message.error(res.msg);
              }
            })
          }  
        })
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      toregister(){
        this.$router.push({path:'/register'})
      },

        
    }

}
</script>

<style scoped>


.el-form-item {
  margin-bottom: 22px; /*  18px */

}              
 
</style>
