<template>
 <div >
    <div style="margin-top:20px">
        <div style="margin-bottom:20px ;font-size:20px;text-align:left">{{ applications.length }}</div>
        <el-row :gutter="20">
            <el-col v-for="application in applications" :key="application.recordId" :xs="24" :sm="12" :md="8" :lg="6">
                <el-card class="application-card" shadow="hover" style="text-align:left">
                    <!--  -->
                    <div slot="header" class="card-header">
                    <span>{{ application.applyId }}</span>
                    <el-button type="warning" size="mini" effect="dark" style="float:right" @click="pendingDetail(application)"></el-button>
                    </div>
                    <!--  -->
                    <div>
                        <div>{{ application.type }}</div>
                        <div>{{application.pname}}</div>
                        <div>{{application.deposityOld}}{{application.deposityNew}}</div>
                        <div>{{ application.applyTime }}</div>
                        <div>{{ application.reviewId }}</div>
                    </div>
                </el-card>
            </el-col>
        </el-row>
    </div>


    <div style="margin-top:20px">
        <div style="margin-bottom:20px ;font-size:20px;text-align:left">{{ completeds.length }}</div>
        <el-row :gutter="20">
            <el-col v-for="completed in completeds" :key="completed.recordId" :xs="24" :sm="12" :md="8" :lg="6">
                <el-card class="completed-card" shadow="hover" style="text-align:left">
                    <!--  -->
                    <div slot="header" class="card-header">
                    <span>{{ completed.applyId }}</span>
                    <el-button type="success" size="mini" effect="dark" style="float:right" @click="completedDetail(completed)"></el-button>
                    </div>
                    <!--  -->
                    <div>
                        <div>{{ completed.type }}</div>
                        <div>{{completed.pname}}</div>
                        <div>{{completed.deposityOld}}{{completed.deposityNew}}</div>
                        <div>{{ completed.applyTime }}</div>
                        <div>{{ completed.reviewId }}</div>
                        <div>{{completed.reviewTime }}</div>
                    </div>
                </el-card>
            </el-col>
        </el-row>
    </div>

    <div style="margin-top:20px">
        <div style="margin-bottom:20px ;font-size:20px;text-align:left">{{ rejecteds.length }}</div>
        <el-row :gutter="20">
            <el-col v-for="rejected in rejecteds" :key="rejected.recordId" :xs="24" :sm="12" :md="8" :lg="6">
                <el-card class="rejected-card" shadow="hover" style="text-align:left">
                    <!--  -->
                    <div slot="header" class="card-header">
                    <span>{{ rejected.applyId }}</span>
                    <el-button type="danger" size="mini" effect="dark" style="float:right" @click="rejectedDetail(rejected)"></el-button>
                    </div>
                    <!--  -->
                    <div>
                        <div>{{ rejected.type }}</div>
                        <div>{{rejected.pname}}</div>
                        <div>{{rejected.deposityOld}}{{rejected.deposityNew}}</div>
                        <div>{{ rejected.applyTime }}</div>
                        <div>{{ rejected.reviewId }}</div>
                        <div>{{rejected.reviewTime }}</div>
                    </div>
                </el-card>
            </el-col>
        </el-row>
    </div>



    <!--  -->
<el-dialog title="" :visible.sync="editFormVisible" width="40%">
<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm" size="small" >
  <div style="margin:25px ; text-align:center;justify-content:center;display:flex" >
          <img v-if="ruleForm.picture" :src="ruleForm.picture" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
  </div>
  <el-form-item label="" prop="pname">
    <el-input v-model="ruleForm.pname"></el-input>
  </el-form-item>
  <el-form-item label="" prop="quantity">
    <el-input v-model.number="ruleForm.quantity" type="number" min="0"></el-input>
  </el-form-item>
  <el-form-item label="" prop="price">
    <el-input v-model.number="ruleForm.price" ></el-input>
  </el-form-item>
  <el-form-item label="" prop="deposityOld">
    <el-select v-model="ruleForm.deposityOld" placeholder="" style="width:100%" clearable>
      <el-option v-for="item in ['1','2','3','4','5']" :key="item" :label="item" :value="item"></el-option>
    </el-select>
  </el-form-item>
  <el-form-item label="" prop="deposityNew">
    <el-select v-model="ruleForm.deposityNew" placeholder="" style="width:100%" clearable>
      <el-option v-for="item in ['1','2','3','4','5']" :key="item" :label="item" :value="item"></el-option>
    </el-select>
  </el-form-item>
  <el-form-item label="" prop="type">
    <el-radio-group v-model="ruleForm.type">
      <el-radio label=""></el-radio>
      <el-radio label=""></el-radio>
      <el-radio label=""></el-radio>
    </el-radio-group>
  </el-form-item>
    <el-form-item label="" prop="reviewId">
    <el-select v-model="ruleForm.reviewId" placeholder="" style="width:100%">
      <el-option v-for="item in ['2001','2002','2003','7933']" :key="item" :label="item" :value="item"></el-option>
    </el-select>
  </el-form-item>
  <el-form-item label="" >
    <el-input type="textarea" v-model="ruleForm.applyRemark"></el-input>
  </el-form-item>
</el-form>
    <div slot="footer" class="dialog-footer">
        <el-button @click="editFormVisible = false"> </el-button>
        <el-button type="primary" @click="apply"> </el-button>
    </div>
</el-dialog>


    <!--  -->
<el-dialog title="" :visible.sync="completedFormVisible" width="40%">
<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm" size="small" >
  <div style="margin:25px ; text-align:center;justify-content:center;display:flex" >
          <img v-if="ruleForm.picture" :src="ruleForm.picture" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
  </div>
  <el-form-item label="">
    <el-input v-model="ruleForm.pname" disabled></el-input>
  </el-form-item>
  <el-form-item label="">
    <el-input v-model.number="ruleForm.quantity" type="number" min="0" disabled></el-input>
  </el-form-item>
  <el-form-item label="">
    <el-input v-model.number="ruleForm.price" disabled></el-input>
  </el-form-item>
  <el-form-item label="" prop="deposityOld">
    <el-select v-model="ruleForm.deposityOld" style="width:100%" disabled>
      <el-option v-for="item in ['1','2','3','4','5']" :key="item" :label="item" :value="item"></el-option>
    </el-select>
  </el-form-item>
  <el-form-item label="">
    <el-select v-model="ruleForm.deposityNew" style="width:100%" disabled>
      <el-option v-for="item in ['1','2','3','4','5']" :key="item" :label="item" :value="item"></el-option>
    </el-select>
  </el-form-item>
  <el-form-item label="">
    <el-radio-group v-model="ruleForm.type" disabled>
      <el-radio label=""></el-radio>
      <el-radio label=""></el-radio>
      <el-radio label=""></el-radio>
    </el-radio-group>
  </el-form-item>
    <el-form-item label="">
    <el-select v-model="ruleForm.reviewId" placeholder="" style="width:100%" disabled>
      <el-option v-for="item in ['2001','2002','2003','7933']" :key="item" :label="item" :value="item"></el-option>
    </el-select>
  </el-form-item>
  <el-form-item label="" >
    <el-input type="textarea" v-model="ruleForm.applyRemark" disabled></el-input>
  </el-form-item>
  <el-form-item label="" >
    <el-input type="textarea" v-model="ruleForm.reviewRemark" disabled></el-input>
  </el-form-item>
</el-form>
    <div slot="footer" class="dialog-footer">
        <el-button @click="completedFormVisible = false"> </el-button>
    </div>
</el-dialog>

    <!--  -->
<el-dialog title="" :visible.sync="rejectedFormVisible" width="40%">
<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm" size="small" >
  <div style="margin:25px ; text-align:center;justify-content:center;display:flex" >
          <img v-if="ruleForm.picture" :src="ruleForm.picture" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
  </div>
  <el-form-item label="">
    <el-input v-model="ruleForm.pname" disabled></el-input>
  </el-form-item>
  <el-form-item label="">
    <el-input v-model.number="ruleForm.quantity" type="number" min="0" disabled></el-input>
  </el-form-item>
  <el-form-item label="">
    <el-input v-model.number="ruleForm.price" disabled></el-input>
  </el-form-item>
  <el-form-item label="" prop="deposityOld">
    <el-select v-model="ruleForm.deposityOld" style="width:100%" disabled>
      <el-option v-for="item in ['1','2','3','4','5']" :key="item" :label="item" :value="item"></el-option>
    </el-select>
  </el-form-item>
  <el-form-item label="">
    <el-select v-model="ruleForm.deposityNew" style="width:100%" disabled>
      <el-option v-for="item in ['1','2','3','4','5']" :key="item" :label="item" :value="item"></el-option>
    </el-select>
  </el-form-item>
  <el-form-item label="">
    <el-radio-group v-model="ruleForm.type" disabled>
      <el-radio label=""></el-radio>
      <el-radio label=""></el-radio>
      <el-radio label=""></el-radio>
    </el-radio-group>
  </el-form-item>
    <el-form-item label="">
    <el-select v-model="ruleForm.reviewId" placeholder="" style="width:100%" disabled>
      <el-option v-for="item in ['2001','2002','2003','7933']" :key="item" :label="item" :value="item"></el-option>
    </el-select>
  </el-form-item>
  <el-form-item label="" >
    <el-input type="textarea" v-model="ruleForm.applyRemark" disabled></el-input>
  </el-form-item>
   <el-form-item label="" >
    <el-input type="textarea" v-model="ruleForm.reviewRemark" disabled></el-input>
  </el-form-item>
</el-form>
    <div slot="footer" class="dialog-footer">
        <el-button @click="rejectedFormVisible = false"> </el-button>
    </div>
</el-dialog>





 </div>
</template>

<script>
import request from '@/request/request'
 export default {
    name:'app',
    data () {
       return {
        applications: [],
        completeds: [],
        rejecteds: [],
 
        editFormVisible: false,
        completedFormVisible:false,
        rejectedFormVisible:false,

        ruleForm: {
          pname: '',
          type: '',
          deposityOld:'',
          deposityNew: '',
          quantity: '',
          price: '',
          state:'',
          applyId:'',
          applyTime: '',
          applyRemark: '',
          reviewId: '',
          reviewRemark: '',
          picture: '',
        },
        rules: {
          pname: [
            { required: true, message: '', trigger: 'blur' }, 
        ],
          quantity:[
            { required: true, message: '', trigger: 'blur' },
            { validator: (rule, value, callback) => {
                if (typeof value !== 'number' || isNaN(value)) {
                    callback(new Error(''));
                } else if (value < 0) {
                    callback(new Error(''));
                } else {
                    callback();
                }
              },
            trigger: ['blur'] }
          ],
          price:[
            { required: true, message: '', trigger: 'blur' },
            { validator: (rule, value, callback) => {
                if (typeof value !== 'number' || isNaN(value)) {
                    callback(new Error(''));
                } else if (value < 0) {
                    callback(new Error(''));
                } else {
                    callback();
                }
              },
            trigger: ['blur'] }
          ],    

          type: [
            { required: true, message: '', trigger: 'change' }
          ],
          reviewId: [ 
            { required: true, message: '', trigger: 'change' }
          ],
        },


       }
    },
    mounted () {
        this.loadpendings();
        this.loadcompleteds();
        this.loadrejecteds();
    },
    methods: { 
        loadpendings(){
            request.get('/recordIn/myPendingData').then(res =>{
            if(res.code==0)
            {
                console.log(res.data)
                this.applications=res.data;
            }
        })
        },
        loadcompleteds(){
            request.get('/recordIn/myCompletedData').then(res =>{
            if(res.code==0)
            {
                this.completeds=res.data;
            }
        })
        },
        loadrejecteds(){
            request.get('/recordIn/myRejectedData').then(res =>{
            if(res.code==0)
            {
                this.rejecteds=res.data;
            }
        })
        },
        pendingDetail(application){//
            console.log("", application);
            this.ruleForm=application;
            this.editFormVisible=true
        },
        completedDetail(completed){//
            console.log("", completed);
            this.ruleForm=completed;
            this.completedFormVisible=true
        },
        rejectedDetail(rejected){//
            console.log("", rejected);
            this.ruleForm=rejected;
            this.rejectedFormVisible=true
        },
        apply(){
         this.$refs.ruleForm.validate((valid) => {
          console.log(this.ruleForm)
                if(valid){
                    this.$request.put('/recordIn/update',this.ruleForm).then(res => {
                      if(res.code===0)
                      {
                        this.$message.success("");
                        this.editFormVisible=false;
                      }else{
                        this.$message.error(res.msg);
                      }
                    })
                }
            })

        },

    }
 }
</script>

<style scoped>
 ::v-deep .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  
   ::v-deep .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
 
</style>
