<template>
 <div>
  
  <div style="display:flex;">
<!--  -->

      <el-input v-model="username" placeholder="" style="width:20%; margin-left: 10px"></el-input>
      <el-input v-model="userid" placeholder="" style="width:20%; margin-left: 10px"></el-input>  
      <el-button type="primary" style="margin-left: 40px" @click="load(1)"><i class="el-icon-search"></i> </el-button>
      <el-button type="primary" style="margin-left: 10px" @click="reset"><i class="el-icon-refresh-right"></i> </el-button>
  </div>
  
  <div style="margin-top: 20px;margin-bottom: 10px">
    <el-button type="primary" plain @click="handleAdd"></el-button>
    <el-button type="danger" plain @click="delBatch"></el-button>
    <el-button type="primary" plain @click="exportData"></el-button>
    <el-upload action="http://localhost:9000/user/import" :headers="{token:user.token}" :on-success="handleImport" style="display:inline-block ;margin-left: 10px" :show-file-list="false">
      <el-button type="primary" plain></el-button>
    </el-upload>
  </div>
  

<!--  -->
  <el-table :data="tableData" stripe border style="width: 100%" 
    :cell-style="{textAlign:'center'}" :header-cell-style="{textAlign:'center',backgroundColor:'aliceblue',color:'#666'}"
    @selection-change="handleSelectionChange">
    <!--  -->
    <el-table-column type="selection" width="55" align="center"> </el-table-column>
    <el-table-column prop="userid" label="id"> </el-table-column>
    <el-table-column prop="username" label="" > </el-table-column>
    <el-table-column prop="role" label="" > </el-table-column>
    <!-- <el-table-column prop="deposity" label="" > </el-table-column> -->
    <el-table-column prop="telephone" label="" > </el-table-column>
    <el-table-column prop="time" label="" :formatter="formatTime"> </el-table-column>
    <el-table-column fixed="right" label="" width="180" >
      <template v-slot="scope">
        <el-button  type="text" size="medium" @click="handleEdit(scope.row)"></el-button>
        <el-button type="text" size="medium" @click="del(scope.row.userid)"></el-button>
      </template>
    </el-table-column>
  </el-table>


<!--  -->
  <div style="margin:10px">
    <el-pagination
      @current-change="handleCurrentChange"
      :current-page="pageNum"
      :page-sizes="[5, 10, 15, 20]"
      :page-size="pageSize"
      layout="total, prev, pager, next"
      :total="total">
    </el-pagination>
  </div>


<!--  -->
<el-dialog title="" :visible.sync="addFormVisible" width="30%">
    <el-form :model="form" :rules="rules" label-width="80px" style="padding-right:20px" ref="form" >
        <el-form-item label="" prop="userid">
            <el-input placeholder="" v-model="form.userid" ></el-input>
        </el-form-item>
        <el-form-item label="" prop="username">
            <el-input placeholder="" v-model="form.username" ></el-input>
        </el-form-item>
        <el-form-item label="" prop="telephone">
            <el-input placeholder="" v-model="form.telephone"></el-input>
        </el-form-item>
        <!-- <el-form-item label="" prop="role">
            <el-input placeholder="" v-model="form.role" ></el-input>
        </el-form-item> -->
        <el-form-item label="" prop="role">
            <el-select v-model="form.role" placeholder="" style="width:100%">
              <el-option v-for="item in ['','','']" :key="item" :label="item" :value="item"></el-option>
            </el-select>
        </el-form-item>
        
        <!-- <el-form-item label="" prop="deposity">
            <el-input placeholder="" v-model="form.deposity" ></el-input>
        </el-form-item> -->
    </el-form>
    <div slot="footer" class="dialog-footer">
        <el-button @click="addFormVisible = false"> </el-button>
        <el-button type="primary" @click="add"> </el-button>
    </div>
</el-dialog>

<!--  -->
<el-dialog title="" :visible.sync="editFormVisible" width="30%">
    <el-form :model="form" :rules="rules" label-width="80px" style="padding-right:20px" ref="form" >
        <el-form-item label="" >
            <el-input placeholder="" v-model="form.userid" disabled></el-input>
        </el-form-item>
        <el-form-item label="" >
            <el-input placeholder="" v-model="form.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="telephone">
            <el-input placeholder="" v-model="form.telephone"></el-input>
        </el-form-item>
        <el-form-item label="" prop="role">
            <el-input placeholder="" v-model="form.role" ></el-input>
        </el-form-item>
        <!-- <el-form-item label="" prop="deposity">
            <el-input placeholder="" v-model="form.deposity" ></el-input>
        </el-form-item> -->
    </el-form>
    <div slot="footer" class="dialog-footer">
        <el-button @click="editFormVisible = false"> </el-button>
        <el-button type="primary" @click="update"> </el-button>
    </div>
</el-dialog>



 </div>
</template>

<script>
import request from '@/request/request'
import dayjs from 'dayjs'
export default {
  created(){
    this.load()
  },
    data() {

      var checkNull = (rule, value, callback) => {
        if (value === '' || value === null || value === undefined) {
          return callback(new Error(''));
        }
        return callback();
      };
      var checkId = (rule, value, callback) => {
        const num = Number(value);
        if (isNaN(num)) { // 
          return callback(new Error('')); // 
        }

        if (!Number.isInteger(num)) { // 
          return callback(new Error('')); // 
        }

        if (value.length !== 4) { // 
          return callback(new Error('4')); // 
        }
        return callback();
      };


      return {
        value1: '',
        tableData: [],
        pageNum: 1,//
        pageSize: 10,//
        username: '',
        userid: '',
        
        total: 0,
        deposity: '',
        editFormVisible:false,
        addFormVisible:false,
        userids: [],
        user:JSON.parse(localStorage.getItem('user') || '{}'),

        form:{
          userid: '',
          username: '',
          telephone: '',
          // deposityValue: '',
          // deposity: '',
        },
        deposityValue:'',
        rules: {
          username: [
            {required: true, validator: checkNull, trigger: 'blur' }
          ],
          role: [
            {required: true, validator: checkNull, trigger: 'blur' }
          ],
          // deposity: [
          //   {required: true, validator: checkNull, trigger: 'blur' }
          // ],
          userid: [
            {required: true, validator: checkId, trigger: 'blur' }
          ],
          telephone: [
            { required: true, message: '', trigger: 'change' },
            { min: 11, max: 11, message: '11', trigger: 'change' }
          ],
        },

      }
    },
    methods: {
      handleImport(res){
        if(res.code===0){
          this.$message.success("");
          this.load(1)
        }else{
          this.$message.error(res.msg);
        }
      },
      exportData(){//
      if(!this.userids.length){//
        window.open('http://localhost:9000/user/export?token='+ this.user.token 
        + "&username="+this.username + "&deposity="+this.deposity + "&userid="+this.userid)
      }else{
        let useridsStr = this.userids.join(',')
        window.open('http://localhost:9000/user/export?token='+ this.user.token + "&userids="+useridsStr)//
      }

      },
      delBatch(){
        if(!this.userids.length)
        {
          this.$message.warning("");
          return
        }
        this.$confirm('?', '', {type:"warning"}).then(() => {
          this.$request.delete('/user/delete/batch',{
            data:this.userids
          }).then(res =>{
            if(res.code===0)
            {
              this.$message.success("");
              this.load(1)
            }else{
              this.$message.error(res.msg);
            }
          })
        }).catch(() => {})
      },
      handleSelectionChange(rows){   
        this.userids=rows.map(v=>v.userid)//
      },
      del(userid){
        this.$confirm('?', '', {type:"warning"}).then(() => {
          this.$request.delete('/user/delete/'+ userid).then(res =>{
            if(res.code===0)
            {
              this.$message.success("");
              this.load(1)
            }else{
              this.$message.error(res.msg);
            }
          })
        }).catch(() => {})
      },
      handleEdit(row){//
      this.form=JSON.parse(JSON.stringify(row))//form
      this.editFormVisible=true
      },
      handleAdd(){//
        this.form={}
        this.addFormVisible=true

      },
      update(){
         this.$refs.form.validate((valid) => {
                if(valid){
                    this.$request.put('user/update',this.form).then(res => {
                      if(res.code===0)
                      {
                        this.$message.success("");
                        this.editFormVisible=false
                        this.load(1)
                      }else{
                        this.$message.error(res.msg);
                      }
                    })
                }
            })
      },
      add(){
         this.$refs.form.validate((valid) => {
                if(valid){
                    this.$request.post('user/add',this.form).then(res => {
                      if(res.code===0)
                      {
                        this.$message.success("");
                        this.addFormVisible=false
                        this.load(1)
                      }else{
                        this.$message.error(res.msg);
                      }
                    })
                }
            })
      },
      reset(){
        this.username=''
        this.userid=''
        // this.deposity=''
        this.load(this.pageNum)
      },
      load(pageNum){//
      if(pageNum)
      {
        this.pageNum=pageNum
      }
        request.get('/user/selectByPage', {
          params: {
            pageNum: this.pageNum,
            pageSize: this.pageSize,
            userid: this.userid,
            username: this.username,
            role: this.user.role,
            // deposity:this.deposity
          }
        }).then(res => {
          if(res.code == 0){
            this.tableData = res.data.records
            this.total=res.data.total
          }
          else{
            this.$notify.error(res.msg);
          }
    
        })
      },
      formatTime(row, column, cellValue) {
      if (!cellValue) return ''; // 
      return dayjs(cellValue).format('YYYY-MM-DD');
    },
    handleCurrentChange(pageNum){
      this.pageNum=pageNum
      this.load(pageNum)
    },

    },
  }
</script>

<style scoped>
.title{
  text-align:left;
}
 
</style>
