<script>
import request from '@/request/request'

 export default {
  created(){
    this.load();
  },
    data() {
      return {
        value1: '',
        tableData: [],
        pageNum: 1,//
        pageSize: 10,//
        operation: '',
        id: '',
        total: 0,
        ids: [],
        user:JSON.parse(localStorage.getItem('user') || '{}'),
        type: '',
        OptUser: '', 

      }
    },
    methods: {
      //   delBatch(){
      //   if(!this.ids.length)
      //   {
