      //       data:this.ids
      //     }).then(res =>{
      //       if(res.code===0)
      //       {
      //         this.$message.success("");
      //         this.load(1)
      //       }else{
      //         this.$message.error(res.msg);
      //       }
      //     })
      //   }).catch(() => {})
      // },
