<template>
 <div style="margin-top: 20px;">

<el-steps :active="active" finish-status="success" align-center>
  <el-step title=""></el-step>
  <el-step title=""></el-step>
  <!-- <el-step title=""></el-step> -->
</el-steps>

<!--  -->
<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm" size="small" style="width:600px">
  <el-form-item label="" prop="pname">
    <el-input v-model="ruleForm.pname" ></el-input>
  </el-form-item>
  <el-form-item label="" prop="picture" style="text-align:left"> 
          <el-upload
            class="picture-uploader"
            action="http://localhost:9000/file/upload"
            :headers="{ token:user.token }"
            :file-list="ruleForm.picture ? [ruleForm.picture] : []"
            list-type="picture"
            :on-success="handleAvatarSuccess">
            <el-button type="primary"></el-button>
          </el-upload>
  </el-form-item>
  <el-form-item label="" prop="type">
    <el-radio-group v-model="ruleForm.type">
      <el-radio label=""></el-radio>
      <el-radio label=""></el-radio>
      <el-radio label=""></el-radio>
    </el-radio-group>
  </el-form-item>
  <!-- <el-form-item label="" prop="stockid">
    <el-input v-model="ruleForm.stockid"></el-input>
  </el-form-item> -->
  <el-form-item label="" prop="quantity">
    <el-input v-model.number="ruleForm.quantity" type="number" min="0"></el-input>
  </el-form-item>
  <el-form-item label="" prop="price">
    <el-input v-model.number="ruleForm.price" ></el-input>
  </el-form-item>
  
  <el-form-item label="" prop="deposityOld" >
    <el-select v-model="ruleForm.deposityOld" placeholder="" clearable>
      <el-option v-for="item in ['','1','2','3','4','5']" :key="item" :label="item" :value="item"></el-option>
    </el-select>
  </el-form-item>
  <el-form-item label="" prop="deposityNew">
    <el-select v-model="ruleForm.deposityNew" placeholder="" clearable>
      <el-option v-for="item in ['','1','2','3','4','5']" :key="item" :label="item" :value="item"></el-option>
    </el-select>
  </el-form-item>
    <el-form-item label="" prop="reviewId">
    <el-select v-model="ruleForm.reviewId" placeholder="">
      <el-option v-for="item in ['2001','2002','2003','7933']" :key="item" :label="item" :value="item"></el-option>
    </el-select>
  </el-form-item>
  <el-form-item label="" >
    <el-input type="textarea" v-model="ruleForm.applyRemark"></el-input>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="apply"></el-button>
    <el-button @click="resetForm('ruleForm')"></el-button>
  </el-form-item>
</el-form>



<!-- <el-button style="margin-top: 12px;" @click="next"></el-button> -->

 </div>
</template>

<script>

 export default {
    
    data () {
       return {
        active: 0,
        user:JSON.parse(localStorage.getItem('user') || '{}'),
        ruleForm: {
          pname: '',
          type: '',
          // deposityOld:this.user.deposity,
          deposityOld:'',
          deposityNew: '',
          quantity: '',
          price: '',
          state:'',
          // applyId:this.user.userid,
          applyId:'',
          applyTime: '',
          applyRemark: '',
          reviewId: '',
          stockid: '',
          picture: '',
        },
        rules: {
          pname: [
            { required: true, message: '', trigger: 'blur' }, 
        ],
        stockid: [
            { required: true, message: '', trigger: 'blur' }, 
        ],
          quantity:[
            { required: true, message: '', trigger: 'blur' },
            { validator: (rule, value, callback) => {
                if (typeof value !== 'number' || isNaN(value)) {
                    callback(new Error(''));
                } else if (value < 0) {
                    callback(new Error(''));
                } else {
                    callback();
                }
              },
            trigger: ['blur'] }
          ],
          price:[
            { required: true, message: '', trigger: 'blur' },
            { validator: (rule, value, callback) => {
                if (typeof value !== 'number' || isNaN(value)) {
                    callback(new Error(''));
                } else if (value < 0) {
                    callback(new Error(''));
                } else {
                    callback();
                }
              },
            trigger: ['blur'] }
          ],    
          // deposityNew: [
          //   { required: true, message: '', trigger: 'change' }
          // ],
          type: [
            { required: true, message: '', trigger: 'change' }
          ],
          reviewId: [ 
            { required: true, message: '', trigger: 'change' }
          ],
          deposityOld: [ 
            { required: true, message: '', trigger: 'change' }
          ],
          deposityNew: [ 
            { required: true, message: '', trigger: 'change' }
          ],
        
        }
       }
    },
    components: {
        

    },
    methods: { 
      handleAvatarSuccess(response){
        this.ruleForm.picture = response.data
        console.log(response.data)

      },
        next() {
            this.active=1;
        },
        apply(){
         this.$refs.ruleForm.validate((valid) => {
          this.ruleForm.applyId=this.user.userid;
          // if(this.ruleForm.type==''){
          //   this.ruleForm.deposityNew=this.user.deposity;
          // }else{
          //   this.ruleForm.deposityOld=this.user.deposity;
          // }

          console.log(this.ruleForm)
                if(valid){
                    this.$request.post('/recordIn/add',this.ruleForm).then(res => {
                      console.log(res)
                      if(res.code===0)
                      {
                        this.$message.success("");
                        this.active=1;
                      }else{
                        this.$message.error(res.msg);
                      }
                    })
                }
            })

        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
        }
    }
 }
</script>

<style scoped>
.demo-ruleForm{
    margin: 0 auto;        /*  */
    padding: 20px;
    margin-top:40px
}

.el-form-item {
  margin-bottom: 22px; /*  18px */
  text-align: left;
}
 
</style>
