<template>
 <div id='user_register'>
  <div :style="{ backgroundImage: `url('${require('@/assets/background.jpg')}'` }" style="height:100vh; display:flex; justify-content:center; align-items:center;background-size:cover">     
    <div style="display:flex; align-items:center;justify-content:center; background-color:white; width:30% ; height:70%; border-radius:5px">
      <el-form :model="ruleForm" style="width:80%;" :rules="rules" ref="ruleForm"  class="demo-ruleForm" >
      <!-- <el-form > -->
        <div style="font-size:20px ;font-weight:bold ; text-align:center;"></div>
        <el-form-item prop="userid" style="margin-top:20px;">
            <el-input placeholder=" "  v-model="ruleForm.userid"></el-input>
        </el-form-item>
        
        <el-form-item prop="username">
            <el-input placeholder=" " v-model="ruleForm.username"></el-input>
        </el-form-item>
        

        <el-form-item prop="password">
            <el-input show-password placeholder=" " type="password" v-model="ruleForm.password" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item prop="checkPass">
            <el-input show-password placeholder=" " type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
        </el-form-item>
        
        <!-- <el-select v-model="ruleForm.deposityValue" @change="handleDepositChange" placeholder="" style="width:100%;">
          <el-option  v-for="item in options" :key="item.value" :label="item.label" :value="item.value" >
          </el-option>
        </el-select> -->

        <el-form-item prop="telephone">
            <el-input placeholder=" " v-model="ruleForm.telephone"></el-input>
        </el-form-item>

        <el-form-item>                      
            <el-button type="primary" @click="register"></el-button>
            <el-button @click="resetForm('ruleForm')"></el-button>
        </el-form-item>
        <div> <p class="text-center mt-3">? <a href="#"  @click="tologin"></a></p></div>
      </el-form>
    </div>
  </div>
 </div>
</template>

<script>
import request from '@/request/request';
// import request from '@/request/request'
 export default {
    name:'user_register',
    data () {
      var checkName = (rule, value, callback) => {
        if (value=='') {
          return callback(new Error(''));
        }
        return callback();
      };
      var checkId = (rule, value, callback) => {
        const num = Number(value);
        if (isNaN(num)) { // 
          return callback(new Error('')); // 
        }

        if (!Number.isInteger(num)) { // 
          return callback(new Error('')); // 
        }

        if (value.length !== 4) { // 
          return callback(new Error('4')); // 
        }
        return callback();
      };
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          return callback(new Error(''));
        } else {
          if (this.ruleForm.checkPass !== '') {
            this.$refs.ruleForm.validateField('checkPass');
          }
          return callback();
        }
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          return callback(new Error(''));
        } else if (value !== this.ruleForm.password) {
          return callback(new Error('!'));
        } else {
          return callback();
        }
      };



       return {
        ruleForm: {
          password: '',
          checkPass: '',
          userid: '',
          username: '',
          telephone: '',
          deposityValue: '',
          deposity: '',
       },
       rules: {
          password: [
            { validator: validatePass, trigger: 'blur' }
          ],
          checkPass: [
            { validator: validatePass2, trigger: 'blur' }
          ],
          username: [
            { validator: checkName, trigger: 'blur' }
          ],
          userid: [
            { validator: checkId, trigger: 'change' }
          ],
          telephone: [
            { required: true, message: '', trigger: 'change' },
            { min: 11, max: 11, message: '11', trigger: 'change' }
          ],
        },

       };
    },
    components: {

    },
    methods: { 
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      tologin(){
        this.$router.push({path:'/login'})
      },
      register(){
        this.$refs["ruleForm"].validate((valid) => {
          
          if (valid){
            request.post('/user/register',this.ruleForm).then(res=>{
              console.log("ruleForm:",this.ruleForm);
              if(res.code==0){
                this.$message.success("");
                this.$router.push({path:'/login'})
              }else{
                this.$message.error(res.msg);
              }
              
            })
          }
        })
      },

      handleDepositChange(value) {
      //  value  label
      const selectedOption = this.options.find(item => item.value === value);
      if (selectedOption) {
        this.ruleForm.deposity = selectedOption.label; //  label  ruleForm
      }
    }

    }

}
</script>

<style scoped>
/* .demo-ruleForm{
    margin: 0 auto;      
    padding: 20px;
    margin-top:250px;
} */

.el-form-item {
  margin-top: 22px;
}              
 
</style>
