<template>
 <div>
  
  <div style="margin-top:20px; margin-bottom: 30px; display:flex;">
      <el-input v-model="dname" placeholder="" style="width:20%; margin-left: 10px"></el-input>
      <el-input v-model="address" placeholder="" style="width:20%; margin-left: 10px"></el-input>
      <el-button type="primary" style="margin-left: 40px" @click="load(1)"><i class="el-icon-search"></i> </el-button>
      <el-button type="primary" style="margin-left: 10px" @click="reset"><i class="el-icon-refresh-right"></i> </el-button>
  </div>


  <div style="margin-top: 20px;margin-bottom: 10px">
    <el-button type="primary" plain @click="handleAdd"></el-button>
    <el-button type="danger" plain @click="delBatch" v-if="user.roleid!=1"></el-button>
    <el-button type="primary" plain @click="exportData"></el-button>
    <el-upload action="http://localhost:9000/deposity/import" :headers="{token:user.token}" :on-success="handleImport" style="display:inline-block ;margin-left: 10px" :show-file-list="false">
      <el-button type="primary" plain></el-button>
    </el-upload>
  </div>
  

<!--  -->
  <el-table :data="tableData" stripe border style="width: 100%" 
    :cell-style="{textAlign:'center'}" :header-cell-style="{textAlign:'center',backgroundColor:'aliceblue',color:'#666'}"
    @selection-change="handleSelectionChange">
    <!--  -->
    <el-table-column type="selection" width="60" align="center"> </el-table-column>
    <el-table-column prop="deposityid" label="" > </el-table-column>
    <el-table-column prop="dname" label="" > </el-table-column>
    <el-table-column prop="address" label="" > </el-table-column>
    <el-table-column prop="introduce" label="" > </el-table-column>
    <el-table-column fixed="right" label="" width="180">
      <template slot-scope="scope">
         <el-button  type="text" size="medium" @click="handleEdit(scope.row)"></el-button>
        <el-button type="text" size="medium" @click="del(scope.row.id)"></el-button>
      </template>
    </el-table-column>
  </el-table>


<!--  -->
  <div style="margin:10px">
    <el-pagination
      @current-change="handleCurrentChange"
      :current-page="pageNum"
      :page-sizes="[5, 10, 15, 20]"
      :page-size="pageSize"
      layout="total, prev, pager, next"
      :total="total">
    </el-pagination>
  </div>


  <!--  -->
<el-dialog title="" :visible.sync="addFormVisible" width="30%">
    <el-form :model="form" :rules="rules" label-width="80px" style="padding:20px" ref="form" >
        <el-form-item label="" prop="dname">
            <el-input placeholder="" v-model="form.dname" ></el-input>
        </el-form-item>
        <el-form-item label="" prop="address">
            <el-input placeholder="" v-model="form.address" ></el-input>
        </el-form-item>
        <el-form-item label="" prop="introduce">
            <el-input placeholder="" v-model.number="form.introduce"></el-input>
        </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
        <el-button @click="addFormVisible = false"> </el-button>
        <el-button type="primary" @click="add"> </el-button>
    </div>
</el-dialog>

<!--  -->
<el-dialog title="" :visible.sync="editFormVisible" width="30%">
    <el-form :model="form" :rules="rules" label-width="80px" style="padding:20px" ref="form" >
        <el-form-item label="" prop="dname">
            <el-input placeholder="" v-model="form.dname" ></el-input>
        </el-form-item>
        <el-form-item label="" prop="address">
            <el-input placeholder="" v-model="form.address" ></el-input>
        </el-form-item>
        <el-form-item label="" prop="introduce">
            <el-input placeholder="" v-model.number="form.introduce"></el-input>
        </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
        <el-button @click="editFormVisible = false"> </el-button>
        <el-button type="primary" @click="update"> </el-button>
    </div>
</el-dialog>




 </div>
</template>

<script>
import request from '@/request/request'

 export default {
  created(){
    this.load();
  },
    data() {
      return {
        tableData: [],
        pageNum: 1,//
        pageSize: 5,//
        dname: '',
        deposityid: '',
        total: 0,
        deposityids: [],
        user:JSON.parse(localStorage.getItem('user') || '{}'),
        address: '',
        introduce: '', 
        form:{
          dname: '',
          address: '',
          introduce: '',
        },
        addFormVisible:false,
        editFormVisible:false,
  

      }
    },
    methods: {
        delBatch(){
        if(!this.deposityids.length)
        {
          this.$message.warning("");
          return
        }
        this.$confirm('?', '', {type:"warning"}).then(() => {
          this.$request.delete('/deposity/delete/batch',{
            data:this.deposityids
          }).then(res =>{
            if(res.code===0)
            {
              this.$message.success("");
              this.load(1)
            }else{
              this.$message.error(res.msg);
            }
          })
        }).catch(() => {})
      },
        del(deposityid){
        this.$confirm('?', '', {type:"warning"}).then(() => {
          this.$request.delete('/deposity/delete/'+ deposityid).then(res =>{
            if(res.code===0)
            {
              this.$message.success("");
              this.load(1)
            }else{
              this.$message.error(res.msg);
            }
          })
        }).catch(() => {})
      },
      reset(){
        this.operation=''
        this.dname=''
        this.address=''
        this.load(this.pageNum)
      },
      handleImport(res){
        if(res.code===0){
          this.$message.success("");
          this.load(1)
        }else{
          this.$message.error(res.msg);
        }
      },
      exportData(){//
      if(!this.deposityids.length){//
        window.open('http://localhost:9000/deposity/export?token='+ this.user.token + "&dname="+this.dname + "&address="+this.address )
      }else{
        let idsStr = this.deposityids.join(',')
        window.open('http://localhost:9000/deposity/export?token='+ this.user.token + "&ids="+idsStr)//
      }
      },
      handleSelectionChange(rows){   
        this.deposityids=rows.map(v=>v.deposityid)//,v.recordIdrowrecoord_id
      },
      load(pageNum){//
      if(pageNum)
      {
        this.pageNum=pageNum
      }
        request.get('/deposity/selectByPage', {
          params: {
            pageNum: this.pageNum,
            pageSize: this.pageSize,
            dname: this.dname,
            address: this.address,
          }
        }).then(res => {
          if(res.code == 0){
            this.tableData = res.data.records
            this.total=res.data.total
          }
          else{
            this.$notify.error(res.msg);
          }
    
        })
      },
    handleCurrentChange(pageNum){
      this.pageNum=pageNum
      this.load(pageNum)
    },
    handleEdit(row){//
      this.form=JSON.parse(JSON.stringify(row))//form
      this.editFormVisible=true
      },
      handleAdd(){//
        this.form={}
        this.addFormVisible=true

      },

    update(){
         this.$refs.form.validate((valid) => {
                if(valid){
                    this.$request.put('deposity/update',this.form).then(res => {
                      if(res.code===0)
                      {
                        this.$message.success("");
                        this.editFormVisible=false
                        this.load(1)
                      }else{
                        this.$message.error(res.msg);
                      }
                    })
                }
            })
      },
      add(){
         this.$refs.form.validate((valid) => {
                if(valid){
                    this.$request.post('deposity/add',this.form).then(res => {
                      if(res.code===0)
                      {
                        this.$message.success("");
                        this.addFormVisible=false
                        this.load(1)
                      }else{
                        this.$message.error(res.msg);
                      }
                    })
                }
            })
      },

    }
  }
</script>

<style scoped>
.title{
  text-align:left;
}
 
</style>
