<template>
 <div sS>
    <el-card style="width:50%;">
        <el-form :model="user" :rules="rules" label-width="80px" style="padding-right:20px" ref="form" >
            <el-form-item label="" prop="username">
                <el-input placeholder="" v-model="user.username" ></el-input>
            </el-form-item>
            <el-form-item label="" prop="userid">
                <el-input placeholder="" v-model="user.userid" disabled></el-input>
            </el-form-item>
            <!-- <el-form-item label="" prop="password">
                <el-input placeholder="" v-model="user.password" ></el-input>
            </el-form-item> -->
            <el-form-item label="" prop="telephone">
                <el-input placeholder="" v-model="user.telephone" ></el-input>
            </el-form-item>
            <el-form-item label="" prop="roleid">
                <el-input  v-model="user.role" disabled></el-input>
            </el-form-item>
            <el-form-item label="" prop="time">
                <el-input  v-model="formattedTime" disabled></el-input>
            </el-form-item>
            <!-- <el-form-item label="" prop="deposity">
                <el-input placeholder="" v-model="user.depositSy" ></el-input>
            </el-form-item> -->
            <!-- <el-form-item label="" prop="deposity">
                <el-input  v-model="user.deposity" disabled></el-input>
            </el-form-item> -->
            <div>
                <el-button type="primary" @click="update"> </el-button>
            </div>


        </el-form>

    </el-card>

 </div>
</template>

<script>
import dayjs from 'dayjs'
 export default {
    name:'app',
    data () {
        var checkNull = (rule, value, callback) => {
        if (value === '' || value === null || value === undefined) {
          return callback(new Error(''));
        }
        return callback();
      };
        
       return {
        user:JSON.parse(localStorage.getItem('user') || '{}'),
        deposityValue:'',
   
        options: [{
          value: '1',
          label: '1'
        }, {
          value: '2',
          label: '2'
        }, {
          value: '3',
          label: '3'
        }, {
          value: '4',
          label: '4'
        }, {
          value: '5',
          label: '5'
        }],


        rules: {
            telephone: [
                { required: true, message: '', trigger: 'change' },
                { min: 11, max: 11, message: '11', trigger: 'change' }
            ],
            username: [
            {required: true, validator: checkNull, trigger: 'blur' }
          ],
        },
       }
    },
    components: {

    },
    methods: { 
        update(){
            this.$refs.form.validate((valid) => {
                if(valid){
                    this.$request.put('/user/update',this.user).then(res=>{
                        if(res.code==0){
                            this.$message.success('')
                            localStorage.setItem('user',JSON.stringify(this.user))
                        }else{
                            this.$message.error(res.msg)
                        }
                    })
                }
            })
        },

        handleDepositChange(value) {
      //  value  label
      const selectedOption = this.options.find(item => item.value === value);
      if (selectedOption) {
        this.user.deposity = selectedOption.label; //  label  ruleForm
      }
    }

    },
    computed: {
    // 
    formattedTime() {
      return this.user.time 
        ? dayjs(this.user.time).format('YYYY-MM-DD') 
        : '';
    }
  }
 }
</script>

<style scoped>


 ::v-deep .el-form-item__label{
    font-weight: bold;
 }

</style>
