<template>
 <div>
  
  <div style="margin-top:20px; margin-bottom: 30px; display:flex;">
<!--  -->
      <!-- <el-select style="margin-left: 10px;" v-model="value" filterable placeholder="">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select> -->
      <!-- <el-input v-model="deposity" placeholder="" style="width:20%; margin-left: 10px"></el-input> -->
      <el-select v-model="form.deposityNew" placeholder="" style="width:20%; margin-left: 10px">
        <el-option v-for="item in ['1','2','3','4','5']" :key="item" :label="item" :value="item"></el-option>
      </el-select>
      <el-select v-model="form.deposityOld" placeholder="" style="width:20%; margin-left: 10px">
        <el-option v-for="item in ['1','2','3','4','5']" :key="item" :label="item" :value="item"></el-option>
      </el-select>
      <el-select v-model="form.type" placeholder="" style="width:20%; margin-left: 10px">
        <el-option v-for="item in ['','','']" :key="item" :label="item" :value="item"></el-option>
      </el-select>
      
      <div class="block" style="margin-left: 10px">
        <el-date-picker
          v-model="form.applyTime"
          type="date"
          placeholder=""
          value-format="yyyy-MM-dd">
        </el-date-picker>
      </div>

      <el-input v-model="form.pname" placeholder="" style="width:20%; margin-left: 10px"></el-input> 

      <el-button type="primary" style="margin-left: 40px" @click="load(1)"><i class="el-icon-search"></i> </el-button>
      <el-button type="primary" style="margin-left: 10px" @click="reset"><i class="el-icon-refresh-right"></i> </el-button>
  </div>


  <div style="margin-top: 20px;margin-bottom: 10px">
    <!-- <el-button type="primary" plain @click="handleAdd"></el-button>
    <el-button type="danger" plain @click="delBatch"></el-button> -->
    <el-button type="primary" plain @click="exportData"></el-button>
    <el-upload action="http://localhost:9000/recordIn/import" :headers="{token:user.token}" :on-success="handleImport" style="display:inline-block ;margin-left: 10px" :show-file-list="false">
      <el-button type="primary" plain></el-button>
    </el-upload>
  </div>
  

<!--  -->
  <el-table :data="tableData" stripe border style="width: 100%" 
    :cell-style="{textAlign:'center'}" :header-cell-style="{textAlign:'center',backgroundColor:'aliceblue',color:'#666'}"
    @selection-change="handleSelectionChange">
    <!--  -->
    <el-table-column type="selection" width="55" align="center"> </el-table-column>
    <el-table-column prop="recordId" label="" width="90"> </el-table-column>
    <el-table-column prop="pname" label="" > </el-table-column>
    <el-table-column prop="type" label="" > </el-table-column>
    <el-table-column prop="deposityNew" label="" width="90"> </el-table-column>
    <el-table-column prop="deposityOld" label="" width="90"> </el-table-column>
    <el-table-column prop="state" label="" width="90"> </el-table-column>
    <el-table-column prop="applyId" label="" width="90"> </el-table-column>
    <el-table-column prop="applyTime" label="" width="180" > </el-table-column>
    <el-table-column prop="reviewId" label="" width="90"> </el-table-column>
    <el-table-column prop="reviewTime" label="" width="180"> </el-table-column>
    <el-table-column fixed="right" label="" >
      <template slot-scope="scope">
        <el-button  type="text" size="medium" @click="handleDetail(scope.row)"></el-button>
        <!-- <el-button type="text" size="medium" @click="del(scope.row.userid)"></el-button> -->
      </template>
    </el-table-column>
  </el-table>


<!--  -->
  <div style="margin:10px">
    <el-pagination
      @current-change="handleCurrentChange"
      :current-page="pageNum"
      :page-sizes="[5, 10, 15, 20]"
      :page-size="pageSize"
      layout="total, prev, pager, next"
      :total="total">
    </el-pagination>
  </div>

<!--  -->
<el-dialog title="" :visible.sync="detailFormVisible" width="40%">
    <el-form :model="form" :rules="rules" label-width="80px" style="padding-right:20px" ref="form" >
      <div style="margin:25px ; text-align:center;justify-content:center;display:flex" >
          <img v-if="form.picture" :src="form.picture" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
      </div>
        <el-form-item label="" >
            <el-input  v-model="form.recordId" disabled></el-input>
        </el-form-item>
        <el-form-item label="" >
            <el-input v-model="form.pname" disabled></el-input>
        </el-form-item>
        <el-form-item label="" >
            <el-input v-model="form.type" disabled></el-input>
        </el-form-item>
        <el-form-item label="" >
            <el-input v-model="form.deposityOld" disabled></el-input>
        </el-form-item>
        <el-form-item label="" >
            <el-input v-model="form.deposityNew" disabled></el-input>
        </el-form-item>
        <el-form-item label="" >
            <el-input v-model="form.quantity" disabled></el-input>
        </el-form-item>
        <el-form-item label="" >
            <el-input v-model="form.price" disabled></el-input>
        </el-form-item>
        <el-form-item label="" >
            <el-input v-model="form.state" disabled></el-input>
        </el-form-item>
        <el-form-item label="" >
            <el-input v-model="form.applyId" disabled></el-input>
        </el-form-item>
        <el-form-item label="" >
            <el-input v-model="form.applyTime" disabled></el-input>
        </el-form-item>
        <el-form-item label="" >
            <el-input v-model="form.applyRemark" disabled></el-input>
        </el-form-item>
        <el-form-item label="" >
            <el-input v-model="form.reviewId" disabled></el-input>
        </el-form-item>
        <el-form-item label="" >
            <el-input v-model="form.reviewTime" disabled></el-input>
        </el-form-item>
        <el-form-item label="" >
            <el-input v-model="form.reviewRemark" disabled></el-input>
        </el-form-item>
    </el-form>
    <!-- <div slot="footer" class="dialog-footer">
        <el-button @click="editFormVisible = false"> </el-button>
        <el-button type="primary" @click="update"> </el-button>
    </div> -->
</el-dialog>







 </div>
</template>

<script>
import request from '@/request/request'

 export default {
  created(){
    this.load();
  },
    data() {
      return {
        tableData: [],
        pageNum: 1,//
        pageSize: 10,//
        // pname: '',
        // recordId: '',
        total: 0,
        record_ids: [],
        user:JSON.parse(localStorage.getItem('user') || '{}'),
        // deposityNew: '',
        // deposityOld: '',
        // applyTime: '',
        // type: '',
        detailFormVisible: false,
        form:{
          recordId: '',
          pname: '',
          type: '',
          deposityNew: '',
          deposityOld: '',
          quantity: '',
          price: '',
          state: '',
          applyId: '',
          applyTime: '',
          applyRemark: '',
          reviewId: '',
          reviewTime: '',
          reviewRemark: '',

        },

      }
    },
    methods: {
      handleDetail(row){
        this.form=JSON.parse(JSON.stringify(row))//form
        this.detailFormVisible=true
      },
      handleAvatarSuccess(response){
        this.form.picture = response.data
        console.log(response.data)
      },
      reset(){
        this.form.pname=''
        this.form.deposityNew=''
        this.form.deposityOld=''
        this.form.applyTime=''
        this.form.type=''
        this.load(this.pageNum)
      },
      handleImport(res){
        if(res.code===0){
          this.$message.success("");
          this.load(1)
        }else{
          this.$message.error(res.msg);
        }
      },
      exportData(){//
      console.log(this.record_ids.length)
      if(!this.record_ids.length){//
        window.open('http://localhost:9000/recordIn/export?token='+ this.user.token + "&pname="+this.pname + "&deposity="+this.deposity )
      }else{
        let record_idsStr = this.record_ids.join(',')
        window.open('http://localhost:9000/recordIn/export?token='+ this.user.token + "&record_ids="+record_idsStr)//
      }
      },
      handleSelectionChange(rows){   
        console.log(" rows :", rows);
        this.record_ids=rows.map(v=>v.recordId)//,v.recordIdrowrecoord_id
        console.log(this.record_ids[0])
      },
      load(pageNum){//
      if(pageNum)
      {
        this.pageNum=pageNum
      }
        request.get('/recordIn/selectByPage', {
          params: {
            pageNum: this.pageNum,
            pageSize: this.pageSize,
            pname: this.form.pname,
            deposityNew:this.form.deposityNew,
            deposityOld:this.form.deposityOld,
            type:this.form.type,
            applyTime:this.form.applyTime
          }
        }).then(res => {
          if(res.code == 0){
            this.tableData = res.data.records
            this.total=res.data.total
          }
          else{
            this.$notify.error(res.msg);
          }
    
        })
      },
    handleCurrentChange(pageNum){
      this.pageNum=pageNum
      this.load(pageNum)
    },



    }
  }
</script>

<style scoped>
.title{
  text-align:left;
}
   ::v-deep .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  
   ::v-deep .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
 
</style>
