 </div>
</template>

<script>
import request from '@/request/request'
 export default {
    name:'app',
    data () {
       return {
        pendings: [],
        completeds: [],
        rejecteds: [],
        completedFormVisible:false,
        pendingFormVisible:false,
        rejectedFormVisible:false,
        ruleForm: {
          pname: '',
          type: '',
          deposityOld:'',
          deposityNew: '',
          quantity: '',
          price: '',
          state:'',
          applyId:'',
          applyTime: '',
          applyRemark: '',
          reviewId: '',
          reviewRemark: '',
          picture: '',
        },
   

       }
    },
    components: {

    },
    mounted () {
        this.loadpendings();
        this.loadcompleteds();
        this.loadrejecteds();
    },
    methods: { 
      agree(){
        request.post('/recordIn/pendingAgree',this.ruleForm).then(res =>{
          if(res.code==0)
          {
            this.$message.success("");
            this.pendingFormVisible=false
            this.loadpendings();
            this.loadcompleteds();
            this.loadrejecteds();
          }
        })
      },
      reject(){
        console.log("", this.ruleForm);
        request.post('/recordIn/pendingReject',this.ruleForm).then(res =>{
          if(res.code==0)
          {
            this.$message.success("");
            console.log("2", this.ruleForm);
            this.pendingFormVisible=false
            this.loadpendings();
            this.loadcompleteds();
            this.loadrejecteds();
          }
        })
      },
      loadpendings(){
          request.get('/recordIn/allPendingData').then(res =>{
          if(res.code==0)
          {
              this.pendings=res.data;
          }
      })
      },
      loadcompleteds(){
          request.get('/recordIn/allCompletedData').then(res =>{
          if(res.code==0)
          {
              this.completeds=res.data;
          }
      })
      },
      loadrejecteds(){
          request.get('/recordIn/allRejectedData').then(res =>{
          if(res.code==0)
          {
              this.rejecteds=res.data;
          }
      })
      },
      completedDetail(completed){//
          console.log("", completed);
          this.ruleForm=completed;
          this.completedFormVisible=true
      },
      pendingDetail(pending){//
          console.log("", pending);
          this.ruleForm=pending;
          this.pendingFormVisible=true
      },
      rejectedDetail(rejected){//
          console.log("", rejected);
          this.ruleForm=rejected;
          this.rejectedFormVisible=true
      },


    }
 }
</script>

<style scoped>
::v-deep .avatar-uploader-icon {
